html, body {
	height: calc(100% - 1rem);
    max-height: calc(100% - 1rem);
	width: 100%;
    overflow: hidden;
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'monospace', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}
